			.nav-tabs .nav-item .nav-link,
			.tab-content .tab-pane {
				background-color: transparent;
			}
		}
	}
	.kanban-item.gu-mirror {
		background-color: $theme-dark-card-bg;
	}

	// Todo application
	.todo-application {
		.content-area-wrapper {
			.sidebar {
				.todo-form {
					.todo-item-action {
						color: $theme-dark-body-color;
					}
				}
			}
			.content-right {
				.todo-task-list-wrapper {
					background-color: $theme-dark-card-bg !important;
					border-color: $theme-dark-border-color;
				}
				.todo-task-list {
					.todo-item {
						&:not(:first-child) {
							border-color: $theme-dark-border-color;
						}
						&.completed {
							.todo-title {
								color: $theme-dark-text-muted-color;
							}
						}
						&:hover {
							box-shadow: $theme-dark-box-shadow;
						}

						.todo-item-action {
							.todo-item-favorite:not(.text-warning) {
								i,
								svg {
									color: $theme-dark-body-color;
								}
							}
						}
					}
				}
			}
		}
		.todo-item-action {
			.close {
				background-color: transparent !important;
			}
		}
	}
	.todo-item.gu-mirror {
		background-color: $theme-dark-card-bg;
		border-color: $theme-dark-border-color;
		box-shadow: $theme-dark-box-shadow;
		&.completed {
			.todo-title {
				color: $theme-dark-text-muted-color;
			}
		}
	}

	// E-commerce application
	.ecommerce-application {
		.ecommerce-card {
			.btn-light {
				background-color: $theme-dark-body-bg !important;
				color: $white;
				border-color: transparent;
			}
		}

		// main content
		.content-right,
		.content-body {
			// header items
			.ecommerce-header-items {
				.result-toggler {
					.search-results {
						color: $theme-dark-headings-color;
					}

					.shop-sidebar-toggler {
						i,
						svg {
							color: $theme-dark-headings-color;
						}
					}
				}
			}

			// list and grid view
			.grid-view,
			.list-view {
				.ecommerce-card {
					.card-body {
						border-color: $theme-dark-border-color;
					}
					.item-options {
						.wishlist {
							span {
								color: $headings-color;
							}
						}

						.cart {
							span {
								color: $white;
							}
						}
					}
				}
			}
		}
		.grid-view {
			.item-name a,
			.item-price {
				color: $theme-dark-body-color;
			}
		}

		// wishlist-items
		.wishlist-items {
			.ecommerce-card {
				.move-cart {
					.move-to-cart {
						color: $white;
					}
				}
			}
		}

		// checkout
		.product-checkout {
			.checkout-options {
				.coupons {
					input {
						color: $theme-dark-body-color;
						&::placeholder {
							color: $theme-dark-body-color;
						}
					}
				}
			}
		}
		// Ecommerce details
		.app-ecommerce-details {
			.item-features,
			.swiper-responsive-breakpoints.swiper-container .swiper-slide {
				background-color: $theme-dark-body-bg;
			}
		}
	}

	// file manager app
	.file-manager-application {
		.sidebar-file-manager {
			background-color: $theme-dark-card-bg !important;
			.jstree {
				.jstree-container-ul {
					.jstree-anchor {
						color: $theme-dark-body-color;
					}
				}
			}
			.storage-status {
				i,
				svg {
					color: $theme-dark-body-color !important;
				}
			}

			.file-manager-title {
				color: $theme-dark-label-color;
			}

			&.show {
				border: 1px solid $theme-dark-border-color !important;
			}
		}

		.content-area-wrapper {
			border: 1px solid $theme-dark-border-color !important;
		}

		// File Manager App
		.file-manager-main-content {
			border: 1px solid $theme-dark-border-color !important;

			.file-manager-app-searchbar {
				background-color: transparent !important;
				border-bottom: 1px solid $theme-dark-border-color !important;

				.file-manager-toggler {
					color: $theme-dark-body-color !important;
				}
			}

			.file-manager-content-header,
			.file-manager-content-body {
				background-color: $theme-dark-card-bg !important;
			}

			.file-manager-content-header {
				border-color: $theme-dark-border-color !important;
			}

			.file-manager-content-body {
				.drives {
					.card {
						background-color: $theme-dark-table-bg;
					}
				}

				.view-container {
					.file-manager-item {
						.file-logo-wrapper {
							background-color: $theme-dark-body-bg !important;
						}
						&:not(.selected) {
							border-color: $theme-dark-border-color !important;
						}
					}
					&:not(.list-view) {
						.file-manager-item {
							.file-size,
							.file-accessed {
								color: $theme-dark-text-muted-color !important;
							}
						}
					}
				}
			}
		}

		#app-file-manager-info-sidebar {
			.nav-tabs .nav-link,
			.tab-content .tab-pane {
				background-color: transparent;
			}

			.nav-tabs {
				.nav-item:not(.active) {
					.nav-link {
						color: $theme-dark-body-color;
					}
				}
			}
		}
	}

	// calendar
	.app-calendar {
		.app-calendar-sidebar {
			background-color: $theme-dark-card-bg;
			border-color: $theme-dark-border-color;

			.sidebar-content-title {
				color: $theme-dark-label-color;
			}
			.select-all,
			.input-filter {
				~ label {
					color: $theme-dark-label-color;
				}
			}
		}
		.fc {
			.fc-day-today {
				background: $theme-dark-body-bg !important;
				background-color: $theme-dark-body-bg !important;
			}

			.fc-timegrid {
				.fc-scrollgrid-section {
					.fc-col-header-cell,
					.fc-timegrid-axis,
					.fc-daygrid-day {
						background-color: $theme-dark-card-bg;
						border-color: $theme-dark-border-color;
					}
					.fc-day-today {
						background-color: rgba($color: $gray-100, $alpha: 0.12) !important;

						&.fc-col-header-cell {
							background-color: $theme-dark-card-bg !important;
						}
					}
				}
			}
			.fc-popover {
				background: $theme-dark-card-bg;
				border-color: $theme-dark-border-color;
			}
			.fc-popover-header {
				.fc-popover-title,
				.fc-popover-close {
					color: $theme-dark-body-color;
				}
			}
			tbody td,
			thead th {
				border-color: $theme-dark-border-color;
			}

			.fc-scrollgrid {
				border-color: $theme-dark-border-color;
			}

			.fc-list,
			th {
				border-color: $theme-dark-border-color;
				.fc-list-day-cushion {
					background: $theme-dark-body-bg;
				}
				.fc-list-event:hover td {
					background-color: $theme-dark-body-bg;
				}
				.fc-list-event-time {
					color: $theme-dark-body-color;
				}
				.fc-list-event {
					td {
						border-color: $theme-dark-border-color;
					}
				}
			}
		}

		.fc-timegrid-event-harness-inset .fc-timegrid-event,
		.fc-timegrid-event.fc-event-mirror,
		.fc-timegrid-more-link {
			box-shadow: 0 0 0 1px $theme-dark-border-color;
		}
	}

	// Invoice Application
	.invoice-list-wrapper {
		.dataTables_wrapper {
			.invoice-list-dataTable-header {
				background-color: $theme-dark-card-bg;
				border-color: $theme-dark-border-color;
			}
			.invoice-data-table {
				background-color: $theme-dark-card-bg;
				tbody .selected-row-bg {
					background-color: $theme-dark-border-color;
				}
				td {
					background-color: inherit;
				}
			}
		}
	}
	.invoice-edit,
	.invoice-add {
		.invoice-preview-card {
			.invoice-product-details {
				background-color: $theme-dark-body-bg;
				.product-details-border {
					background-color: $theme-dark-card-bg;
					border-color: $theme-dark-border-color;
				}
			}
		}
	}
	.invoice-print {
		.form-control {
			background-color: $theme-dark-card-bg;
		}
	}

	// Users Application
	.app-user-view {
		.plan-card {
			border-color: $primary !important;
		}
	}
	.app-user-edit {
		.form-control.error {
			border-color: $danger !important;
		}
	}

	/********** CUSTOMIZER **********/

	.customizer {
		background-color: $theme-dark-card-bg;

		.customizer-close {
			i,
			svg {
				color: $theme-dark-body-color;
			}
		}
	}

	.footer-fixed {
		.footer {
			background-color: $theme-dark-card-bg;
		}
	}

	/********** HORIZONTAL LAYOUT **********/
	.horizontal-layout {
		&.navbar-sticky .horizontal-menu-wrapper .navbar-horizontal.header-navbar.fixed-top {
			box-shadow: 0 4px 24px 0 rgba($black, 0.75) !important;
		}
		.horizontal-menu-wrapper {
			background: linear-gradient(
				to bottom,
				rgba(37, 43, 71, 0.76) 44%,
				rgba(56, 53, 53, 0.46) 73%,
				rgba($white, 0) 100%
			) !important;
			.header-navbar {
				background: $theme-dark-card-bg !important;
				&.navbar-shadow {
					box-shadow: 0 4px 24px 0 rgba($black, 0.75);
				}
				&.navbar-horizontal {
					ul#main-menu-navigation > li:hover:not(.active) > a {
						background: $theme-dark-body-bg;
					}

					.active {
						.nav-link {
							&.dropdown-toggle::after {
								background-image: url(str-replace(
									str-replace($chevron-down, 'currentColor', $white),
									'#',
									'%23'
								));
							}
						}
					}
					&.navbar-dark {
						background: $theme-dark-card-bg !important;
					}

					.nav-link {
						&.dropdown-toggle::after {
							background-image: url(str-replace(
								str-replace($chevron-down, 'currentColor', $theme-dark-body-color),
								'#',
								'%23'
							));
						}
					}
				}
			}
		}
		.main-menu-content {
			.navbar-nav {
				.dropdown-submenu {
					&.show {
						background-color: $theme-dark-card-bg !important;
						color: $theme-dark-body-color;
					}
				}
				.dropdown-menu {
					a {
						&:hover {
							color: $theme-dark-headings-color !important;
						}
					}
					.disabled a {
						color: $theme-dark-text-muted-color;
					}
					.dropdown-item {
						color: $theme-dark-headings-color;
					}
					.dropdown-toggle {
						&::after {
							background-image: url(str-replace(
								str-replace($chevron-right, 'currentColor', $theme-dark-body-color),
								'#',
								'%23'
							));
						}
						&:hover,
						&:active {
							&::after {
								background-image: url(str-replace(
									str-replace($chevron-right, 'currentColor', $white),
									'#',
									'%23'
								));
							}
						}
					}
				}

				> li.active {
					> a {
						> i,
						> svg {
							color: $white !important;
						}
					}

					.dropdown-menu {
						li.active > a {
							background: $theme-dark-body-bg !important;
							color: $theme-dark-body-color;
							&:hover {
								color: $primary !important;
							}
						}
					}

					.dropdown-menu {
						li.open.active {
							> a {
								color: $theme-dark-body-color !important;
							}
						}
					}
				}
			}
		}
	}
}
