// Sidebar base
// ------------------------------

// Base
.sidebar {
	position: relative;
	width: 100%;
	height: 100%;

	// For desktop only
	@include media-breakpoint-up(lg) {
		vertical-align: top;
	}
}
.sidebar-fixed {
	position: fixed;
	height: 100%;
	overflow: scroll;
}

.sidenav-overlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	height: 120vh;
	background-color: rgba($black, 0.5);
	z-index: 997;
	display: block;
	visibility: hidden;
	opacity: 0;
	transition: all 0.5s ease;
	&.show {
		opacity: 1;
		visibility: visible;
		transition: all 0.5s ease;
	}
}

.drag-target {
	height: 100%;
	width: 40px;
	position: fixed;
	top: 0;
	left: -10px;
	z-index: 1036;
}

.sidebar-detached {
	.sidebar {
		width: $sidebar-width;
	}
}

@include media-breakpoint-up(lg) {
	.sidebar-left {
		float: left;
	}
	.sidebar-right {
		float: right;
	}
}

@include media-breakpoint-down(md) {
	.sidebar-detached {
		.sidebar {
			width: 100%;
		}
	}
}
